/* rwd img css CSS Reset  */
img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}
/*  border box */
*,
*::before,
*::after {
  box-sizing: border-box;
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  margin-bottom: 0px;
  line-height: $line-height-base;
}

body {
  font-family: $en-font-family, $tc-font-family, $font-family-sans-serif;
  line-height: $line-height-base;
  .container {
    max-width: 1140px;
    margin: 0 auto;
    padding: 0px 15px;

    @include desktop {
      max-width: 720px; /* 690 + 30 */
    }
    @include mobile {
      max-width: 375px; /* 345 + 30 */
    }
  }
}
